<template>
	<view class="wrapper">
		<view class="whitebox">
			<view class="listtitle">
				<view class="utitlebox" style="margin-top: 30upx;">
				    <view class="utitleblock"></view>
				    <view class="utitle">商家管理</view>
				</view>
			</view>
			<text class="ts">{{list==0 ? '(点击添加按钮可添加商品)':'(左滑列表可进行操作)'}}</text>
			<u-swipe-action :show="item.show" :index="index" v-for="(item, index) in list" :key="item.id" @click="click" @open="open"
			 :options="options" class="listbox">
				<view class="item u-border-bottom" @click="xq">
					<image mode="aspectFill" :src="item.images" />
					<!-- 此层wrap在此为必写的，否则可能会出现标题定位错误 -->
					<view class="title-wrap">
						<text class="name">{{item.name}}</text>
						<text class="ry">联系人员:{{item.ry}}</text>
						<text class="dh">联系电话:{{item.dh}}</text>
					</view>
				</view>
			</u-swipe-action>


		</view>
		<view class="aa" style="padding-bottom: 150rpx;"></view>
		<u-empty text="暂无商品信息" mode="list" :class="list == 0 ? '':'nolist'"></u-empty>
		<bottomBar></bottomBar>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				list: [{
						id: 1,
						images: 'http://qp9mr1fhs.hn-bkt.clouddn.com/img/sj1.jpg',
						name: '广东省中山市古镇镇香蕉乐园有限公司',
						ry:'吴彦祖',
						dh:'1550877331',
						show: false,
					}
				],
				disabled: false,
				show: false,
				options: [{
						text: '修改',
						style: {
							backgroundColor: '#5fd0bb'
						}
					},
					{
						text: '注销',
						style: {
							backgroundColor: '#dd524d'
						}
					}
				]
			};
		},
		methods: {
			xq(){
				uni.navigateTo({
					url: './business'
				})
			},
			click(index, index1) {
				
				if (index1 == 1) {
					uni.showModal({
						title:'提示',
						content: "您是否注销商家\n需要等待后台审核\n",
						confirmColor:'#0ABB9A',
						success: (res) => {
							if(res.confirm){
								uni.showModal({
									title:"注销申请",
									content:"将在3个工作日内给予您答复\n请多关注公众号推送\n",
									showCancel:false,
									confirmColor:'#0ABB9A',
									success: (res) => {
										if(res.confirm){
											uni.switchTab({
												url:"../my/my"
											})
										}
									}
								})
							}
							
						}
					})

				} else {
					uni.navigateTo({
						url: './business-addition'
					})
				}
			},
			// 如果打开一个的时候，不需要关闭其他，则无需实现本方法
			open(index) {
				// 先将正在被操作的swipeAction标记为打开状态，否则由于props的特性限制，
				// 原本为'false'，再次设置为'false'会无效
				this.list[index].show = true;
				this.list.map((val, idx) => {
					if (index != idx) this.list[idx].show = false;
				})
			}
		}
	};
</script>

<style>
	.inlist {
		margin-top: 250upx;
	}

	.nolist {
		display: none;
	}
	.add{
		display: flex;
		align-items: center;
	}
	.add_img{
		width: 35upx;
		height: 35upx;
	}
	.utitlebox{
		margin-bottom: 10upx;
	}
	.atext {
		font-size: 30upx;
		font-weight: bold;
		color: #000000;
		position: absolute;
		margin-top: -2upx;
		left: 8%;
	}

	.dtext span {
		color: #0ABB9A;
	}

	.item {
		display: flex;
		padding: 20rpx;
	}

	.item image {
		width: 200rpx;
		height: 200rpx;
		margin-right: 20rpx;
		border-radius: 20upx;
		box-shadow: 0 0 12upx #C0C4CC;
	}

	.title {
		text-align: left;
		font-size: 28rpx;
		color: $u-content-color;
		margin-top: 20rpx;
	}

	.title-wrap {
		position: relative;
		width: 360upx;
		height: 170upx;
		display: flex;
		font-size: 28upx;
		color: #424242;
		font-size: 30upx;

	}

	.title-wrap text {
		position: absolute;
	}

	.name {
		top: 3%;
		font-weight: bold;
	}

	.ry{
		top: 55%;
	}
	.dh{
		top: 90%;
	}

	.ts {
		margin-left: 20upx;
		font-size: 25upx;
		color: gray;
		padding-top: 10upx;
		
	}
</style>

